<script>
    import Clipboard from 'clipboard'
    let provider, signer, defaultAccount, networkVersion;
    let usdtContract, usdtAddress = '0x55d398326f99059fF775485246999027B3197955';
    let ftpContract, ftpAddress = '0x3330eB71926973D56DAF67bd5935c33A14f8e464';
    let lpContract, lpAddress = '0xA9199F7f8B05E3E1e830528C3765B49593aE7fC8';
    let nodeContract, nodeAddress = '0xa001a70933807c293d7c52d854d60b49cF0D744D';
    let yaContract, yaAddress = '0x48498Dd7565cF93B9570Fe3d49313593da6AAD4E';
    let chiAddress = "0x449d0B4451ad6BdF8AeC30C6ba205d52a1dC1967";
    let lpUrl = "https://pancakeswap.finance/swap?inputCurrency=0x3330eb71926973d56daf67bd5935c33a14f8e464&outputCurrency=0x55d398326f99059ff775485246999027b3197955";
    let bnbUrl = 'https://bscscan.com/address/';
    let baseUrl = "https://ftprediction.github.io";
    export default {
        data() {
            return {
                lang: 'en',
                lang_list: {},
                address: '',
                short_address: '连接钱包',
                parents:'',
                disable: false,
                show: false,
                meanShow: false,
                type: '',
                num: 1000000,
                isNode:false,
                parentsNode:false,
                ftp:'',
                nodeInfo:{
                  start:0,
                  end:49,
                  rate:0,
                  profit:0,
                },
                nodedisable:false,
                npdisable:false,
                ftpYu:{}, //预售
                ftpYuInfo:{
                    lun:'一',
                    rate:'0',
                    canGet:0,
                    shen:0,
                    price:0,
                }, //预售详情
                yuNum:'',
                ysdisable:false,
                activePrice:100,
                ftpBanner:0,
                lpBanner:0,
                activeTime:1,
                activeProfit:0.1,
                ftpdisable:false,
                fudisable:false,
                gfpdisable:false,
                ftpInfo:{
                    profit:0,
                    sum:0,
                    suoProfit:0,
                    num:0,
                },
                activeLpPrice:100,
                lpdisable:false,
                activeLpProfit:0,
                lpInfo:{
                    lpAmount:0,
                    LPprofit:0,
                },
                lp_rate:0,
                ludisable:false,
                chiBanner:0,
                nodeId:0,
                nodeUrl:0,
            }
        },
        created: function () {
            this.checkNet();
            this.ftp = ftpAddress;
        },
        async mounted() {
            this.parents = this.$route.query.p_address?this.$route.query.p_address:this.getUrlParam('p_address');
            window.addEventListener('load', async () => {
                // Modern dapp browsers...
                if (window.ethereum) {
                    try {
                        window.web3 = new Web3(ethereum);
                        // Request account access if needed
                        await ethereum.enable();
                        // Acccounts now exposed
                        web3.eth.sendTransaction({/* ... */});
                    } catch (error) {
                        // User denied account access...
                    }
                }
                // Legacy dapp browsers...
                else if (window.web3) {
                    window.web3 = new Web3(web3.currentProvider);
                    // Acccounts always exposed
                    web3.eth.sendTransaction({/* ... */});
                }
                // Non-dapp browsers...
                else {
                    console.log('Non-Ethereum browser detected. You should consider trying MetaMask!');
                }
            });
        },
        methods: {
            toTrade(){
               window.open(lpUrl)
            },
            comeIn(){
              this.$toast('即将开放');
            },
            hiddleShow(){
                this.show=false;
            },
            lookNft(){
                this.show=true;
            },
            async lpUnstake(event){
                let _this = this;
                if(this.lpInfo.lpAmount<=0){
                    this.$toast('暂时未发现LP质押数量');
                    return false;
                }
                if(_this.ludisable){
                    return false;
                }
                _this.ludisable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
                try{
                    const gasPrice = await provider.getGasPrice()
                    let res = await yaContract.redeemLP({
                        gasPrice: gasPrice,
                        gasLimit:250000
                    })
                    if(res){
                        _this.ludisable = false;
                        _this.$toast('操作成功');
                        setTimeout(function () {
                            location.reload();
                        },3000)
                    }
                }catch (e) {
                    _this.$toast('操作失败');
                    _this.ludisable = false;
                    setTimeout(function () {
                        location.reload();
                    },3000)
                }
            },
            async submitLp(event){
                let _this = this;
                const gasPrice = await provider.getGasPrice();
                if(this.activeLpPrice>this.lpBanner){
                    this.$toast('Lp余额不足');
                    return false;
                }
                if(this.activeLpPrice<=0){
                    this.$toast('Lp质押数量不能为0');
                    return false;
                }
                if(this.lpdisable){
                    return false;
                }
                this.lpdisable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
