            getUrlParam(name) {
                var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象
                var r = window.location.search.substr(1).match(reg);  //匹配目标参数
                if (r != null) return unescape(r[2]);
                return null; //返回参数值
            },
            async checkNet() {
                let _this = this;
                try {
                    provider = new ethers.providers.Web3Provider(web3.currentProvider);
                    signer = provider.getSigner();
                    _this.checkLogin();
                } catch (e) {
                    _this.$toast('请使用币安智能链链接');
                }

            },
            async checkLogin() {
                let _this = this;
                setInterval(function () {
                    window.ethereum.enable()
                        .then(function (accounts) {
                            if (accounts.length == 0 && defaultAccount != "") {
                                window.location.reload();
                                return;
                            }
                            if (accounts.length == 0) {
                                return;
                            }
                            if (accounts[0] != defaultAccount) {
                                defaultAccount = accounts[0];
                                window.location.reload();
                                console.log("account change, start bat updateData!")
                            }
                        })
                        .catch(function (reason) {
                            // Handle error. Likely the user rejected the login:
                            console.log(reason === "User rejected provider access")
                        })
                }, 1000);
                window.ethereum.enable()
                    .then(async function (accounts) {
                        if (accounts.length == 0) {
                            console.log('33333');
                            return;
                        }
                        defaultAccount = accounts[0];
                        console.log("defaultAccount: ", defaultAccount);
                        networkVersion = window.ethereum.networkVersion
                        if (networkVersion == 1) {  //以太坊主网
                            _this.type = 'ETH';
                            _this.$toast('请使用币安智能链链接');
                            return false;
                        }
                        else if (networkVersion == 42) {   //以太坊测试网  K
                            _this.type = 'ETH';
                            _this.$toast('请使用币安智能链链接');
                            return false;
                        }
                        else if (networkVersion == 128) {  //HECO主网
                            _this.type = 'HT';
                            _this.$toast('请使用币安智能链链接');
                            return false;
                        }
                        else if (networkVersion == 256) { //HECO测试网
                            _this.type = 'HT';
                            _this.$toast('请使用币安智能链链接');
                            return false;
                        }
                        else if (networkVersion == 56) { //币安主网
                            _this.type = 'BNB';
                        }
                        else {
                            _this.$toast('请使用币安智能链链接');
                            return false;
                        }
                        usdtContract = new ethers.Contract(usdtAddress, usdtAbi, signer);
                        ftpContract = new ethers.Contract(ftpAddress, ftpAbi, signer);
                        lpContract = new ethers.Contract(lpAddress, lpAbi, signer);
                        nodeContract = new ethers.Contract(nodeAddress, nodeAbi, signer);
                        yaContract = new ethers.Contract(yaAddress, yaAbi, signer);
                        if (defaultAccount != '') {
                            _this.address = defaultAccount;
                            // 查询节点进度
                            _this.getNode();
                            _this.getLpRate();
                            //获取FTP预售
                            _this.getpreSaleInfo();
                            _this.getFtpBanner();
                            _this.getLpBanner();
                            _this.getfptInfo(_this.address);
                            _this.getChiBanner();
                            _this.getLpInfo(_this.address);
                            setInterval(function () {
                                _this.getChiBanner();
                            },5000)
                            // 获取LP收益率
                            setInterval(function () {
                                _this.getLpInfo(_this.address);
                            },30000)
                        }
                    });
                setTimeout(function () {
                    if (defaultAccount) {
                        _this.address = defaultAccount;
                        _this.short_address = _this.address.substr(0, 3) + '...' + _this.address.substr(_this.address.length - 3)
                    }

                }, 500)
            },
            async getLpInfo(address){
                let _this = this;
                await yaContract.getPlayerByAddr(address).then(res => {
                    _this.lpInfo.LPTime = ethers.utils.formatEther(res[1])*1000000000000000000;
                    _this.lpInfo.dynamicBalance = ethers.utils.formatEther(res[6]);
                    _this.lpInfo.lpAmount = ethers.utils.formatEther(res[7]);
                    _this.lpInfo.LPEndTime = ethers.utils.formatEther(res[9])*1000000000000000000;
                    // 计算LP收益     0.0000025
