                    _this.lpInfo.LPprofit =(parseFloat(_this.lpInfo.dynamicBalance)+parseFloat(parseFloat(_this.lpInfo.lpAmount)*parseFloat(_this.lp_rate)*Math.floor(((timestamp-_this.lpInfo.LPEndTime))/60))).toFixed(6);
                    console.log(_this.lpInfo)
                }).catch(err => {
                    console.log(err);
                    return;
                });
