                    return;
                });
                await nodeContract.stage5().then(res => {
                    let total = 400000;
                    let etherString = ethers.utils.formatEther(res)*1000000000000000000;
                    _this.ftpYu.stage5 = etherString;
                    if(etherString>0&&_this.ftpYu.stage1==0&&_this.ftpYu.stage2==0&&_this.ftpYu.stage3==0&&_this.ftpYu.stage4==0){
                        _this.ftpYuInfo.lun = '五';
                        _this.ftpYuInfo.rate = parseInt((total-parseFloat(etherString))/total*100);
                        _this.ftpYuInfo.shen = etherString;
                        _this.ftpYuInfo.price = 0.0004;
                    }

                }).catch(err => {
                    console.log(err);
                    return;
                });
                $('#yu_img').css('left',"calc("+_this.ftpYuInfo.rate+'% - 9px)');
                $('#yu_right').css('width',(100-_this.ftpYuInfo.rate)+'%');
                $('#yu_left').css('width',_this.ftpYuInfo.rate+'%');
            },
            async getNode(){
                let _this = this;
                await nodeContract._naturalCount().then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.nodeInfo.start = parseInt(ethers.utils.formatEther(res)*1000000000000000000)
                    _this.nodeInfo.rate = parseInt(_this.nodeInfo.start/parseInt(_this.nodeInfo.end)*100);
                    $('#jie_img').css('left',"calc("+_this.nodeInfo.rate+'% - 9px)');
                    // $('#jie_img').css('left',_this.nodeInfo.rate+'%');
                    $('#jie_left').css('width',_this.nodeInfo.rate+'%');
                    $('#jie_right').css('width',(100-_this.nodeInfo.rate)+'%');
                }).catch(err => {
                    console.log(err);
                    return;
                });
                //判断用户是否为节点
                await nodeContract.getIdByAddr(defaultAccount).then(res => {
                    let id = ethers.utils.formatEther(res)*1000000000000000000;
                    if(id>0){
                        _this.isNode = true;
                        _this.nodeId = id;
                        _this.nodeUrl = require('../../assets/img/nft/'+id+'.png');
                        nodeContract._playerMap(id).then(res => {
                            // 计算可领取收益
                            let lastProfit = ethers.utils.formatEther(res.LockWarehouse)*1000000000000000000;
                            if(parseFloat(lastProfit)>0){
                                // let endTime = parseInt(ethers.utils.formatEther(res.investTime)*1000000000000000000)+86400*30;
                                let endTime = parseInt(ethers.utils.formatEther(res.investTime)*1000000000000000000)+200;
                                let nowTime =parseInt((new Date().getTime())/1000);
                                if(nowTime>=endTime){
                                    _this.nodeInfo.profit = lastProfit*0.1;
                                }
                            }
                        }).catch(err => {
                            console.log(err);
                            return;
                        });
                    }
                }).catch(err => {
                    console.log(err);
                    return;
                });
                // 判断邀请用户是否为节点
                if(_this.parents){
                    await nodeContract.getIdByAddr(_this.parents).then(res => {
                        let id = ethers.utils.formatEther(res)*1000000000000000000;
                        if(id>0){
                           _this.parentsNode = true;
                        }
                    }).catch(err => {
                        console.log(err);
                        return;
                    });
                }
               /* await nodeContract.administrators().then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.nodeInfo.end = ethers.utils.formatEther(res)*1000000000000000000
                    console.log(_this.nodeInfo)
                }).catch(err => {
                    console.log(err);
                    return;
                });*/

            },
            async getFtpBanner() {  //获取余额
                let _this = this;
                await ftpContract.balanceOf(defaultAccount).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.ftpBanner = Math.floor(parseFloat(ethers.utils.formatEther(res)) * 100) / 100;
                }).catch(err => {
                    console.log(err);
                    return;
                });
            },
            async getChiBanner() {  //获取余额
                let _this = this;
                await ftpContract.balanceOf(chiAddress).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    let num = Math.floor(parseFloat(ethers.utils.formatEther(res)));
                    if( num>1000){
                        _this.chiBanner = (parseFloat(num)/1000).toFixed(2)+'K';
                    }
                    if(num>1000000){
                        _this.chiBanner = (parseFloat(num)/1000000).toFixed(2)+'M';
                    }
                    console.log(_this.chiBanner)
                }).catch(err => {
                    console.log(err);
                    return;
                });
            },
            async getLpBanner() {  //获取余额
                let _this = this;
                await lpContract.balanceOf(defaultAccount).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.lpbanner = Math.floor(parseFloat(ethers.utils.formatEther(res)) * 100) / 100;
                }).catch(err => {
                    console.log(err);
                    return;
                });
            },
        }
    }
</script>


